.header {
  height: 30px; }

.logo {
  float: left;
  font-weight: 600; }

.links {
  float: right; }

.link {
  float: left;
  margin: 0 15px;
  opacity: 0.7;
  cursor: pointer; }
  .link:hover, .link.selectedLink {
    opacity: 1; }
  .link a:focus {
    outline-width: 0; }

@media (max-width: 600px) {
  .links {
    float: left;
    margin: 15px 0 0 -15px;
    display: block; }
  .logo {
    float: none; } }
